@import '../css/base.css';
:root {
  --c-black: #000;
  --c-white: #fff;
  --c-outside-gray-light: #dbdada;
  --c-outside-gray-med: #d3d6d9;
  --c-outside-gray-dark: #898989;
  --c-outside-gray-darker: #565656;
  --c-bg-gray: #f4f4f4;
  --c-blue: #069;
  --c-blue-slate: #11385b;
  --c-outside-blue: #2c4989;
  --c-sponsor-blue: #069;
  --c-light-blue: #08c;
  --c-red: #9b1e25;
  --c-red2: #9e0f18;
  --c-bright-red: #ed0505;
  --c-light-red: #ee5c61;
  --c-brick: #460d11;
  --c-faux-black: #3d3d3d;
  --c-yellow: #ffd204;
  --c-yellow-light: #ffed9d;
  --c-green: #8fcea6;
  --c-charcoal: #333;
  --c-orange: #e1472c;
}

* {
  box-sizing: border-box;
}

.winter101-app {
  width: 100%;
}

h2 {
  font-size: 2em;
}

.winter101 {
  margin: 0 auto;
  max-width: 1200px;
  width: 100%;
}

.winter101-posts {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  width: 100%;
}